.App {
  text-align: center;
  font-size: 30px;
  text-decoration: none;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Normal {
color: black;
}

.Sky {
color: #0000aa;
}

.Locate {
color: darkgreen;
}

.Wind {
color: darkblue;
}

.Country {
color: darkred;
}

.Cloud {
color: #444444;
}

.maxTemp {
color: red;
}

.minTemp {
color: blue;
}

body {
background-color: #99cccc;
}

.input {
font-size: 19px;
background-color: #99cccc;
text-decoration: none;
}
